*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Roboto', sans-serif;
    background-color: #222222;
    color : white;
}
a{
    text-decoration: none;
    
}   

header {

    background-color: #333333;
    box-shadow: 0 4px 7px rgba(0, 0, 0, 0.25);
}

#navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
    margin: 0 auto;
    max-width: 1240px;
    
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 1rem;
  height: 90px;
 
}
.nav-brand h1{
    font-family:'grenze',serif ;
    font-weight: 400;
}

.nav-list {
    display: flex;
    gap: 2rem;
    list-style: none;
    align-items: center;
}
li a{
    color: white;
font-size: 1.1rem;
transition: 0.3s;
}
li a:hover{
    color: #f1c40f;
    transition: 0.5S;
    font-size: 1.4rem;
}